<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process199"></a>

<p><br></p>
<p><span class="rvts17">用交叉舌谈话（</span><span class="rvts50">Speaking in Forked Tongues</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:590,Y:101</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：会说话的箱子（</span><span class="rvts20">The Speaking Chest</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">想办法打开箱子，获得里面的宝物</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1240.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在石墓花园一处有两个喷火石龙的地方，在火焰堆中，可以找到一个箱子。使用队伍中的蜥蜴角色与它对话，它会要求输入密码才可以打开宝箱。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1241.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">不知道密码怎么办？寻找瑞克房间里那只招眼的红蜥蜴，使用同时具备学者标签和宠物伙伴天赋的蜥蜴角色与它对话，并且需要一定的</span><span class="rvts20"></span><span class="rvts21">劝告（</span><span class="rvts20">Persuasion</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">值才能把密码套出来。</span>
<br><span class="rvts21">套出密码后，再次找到之前发现箱子的地方，使用密码便可以打开箱子。</span>
<br><span class="rvts21">另外，通过吃掉</span><span class="rvts20"></span><span class="rvts21">来自异乡的异人</span><span class="rvts20"></span><span class="rvts21">支线中的那支蜥蜴腿，也可以获得这个箱子的密码，但是会遭到那只蜥蜴的诅咒，并且那条支线将无法完成。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



